/* About Me */
.about-me {
    padding: 100px 30px;
    overflow: hidden;
    h2 {
        font-weight: 600;
    }
    p {
        font-size: 15px;
        line-height: 25px;

    }
    .video {
        position: relative;
        iframe {
            position: absolute;
            top: 45%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 455px;
            height: 260px 
        }
    }
}

/* Wixcard */
.wixcard {
    padding: 100px 30px;
    background-color: rgba(#333, .1);
    overflow: hidden;
    h2 {
        font-size: 22px;
        font-weight: 600
    }
    p {
        font-size: 18px;
        line-height: 25px;
        width: 90%;
        margin-bottom: 80px
    }
    h3 {
        font-size: 18px;
        font-weight: 500;
        margin-bottom: 30px;
        background-color: #333;
        color: #fff;
        padding: 10px;
        width: 25%;
        text-shadow: 0 0 6px #000;
        text-align: center
    }
    span {
        display: block;
        text-align: right;
        padding: 3px 10px;
        margin-bottom: 15px;
        padding-right: 20px;
        position: relative;
        font-size: 15px;
        text-shadow: 0 0 .05px #000;
        &::after {
            content: "";
            position: absolute;
            top: 50%;
            right: 0;
            width: 15px;
            height: 2px;
            background: $mainColor
        }
    }
}

/* Section Three D */
.threeD {
    padding: 100px 30px;
    background-image: url('../images/IMG-20190511-WA0003.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(#333, .5);
        filter: blur(10px)
    }
    .container {
        position: relative;
        z-index: 999;
    }
    p {
        background-color: rgba(#000, .5);
        color: #fff;
        border-radius: 3px;
        line-height: 25px;
        padding: 15px 25px;
        margin-bottom: 60px;
    }
    .accordion {
        .card {
            background-color: rgba(#000, .5);
            color: #fff;
            text-align: right;
            .card-header {
                padding: 0;
            }
            h3 {
                margin: 0;
                padding: 20px;
                .btn-link {
                    color: $mainColor;
                    transition: .2s;
                    text-decoration: none;
                    &:hover {
                        color: $mainColor
                    }
                }
            }
            .card-body {
                font-size: 14px;
                line-height: 28px
            }
        }
    }
    h4 {
        font-size: 18px;
        font-weight: 500;
        margin-bottom: 30px;
        margin-top: 30px;
        background-color: $mainColor;
        border-right: 5px solid #333;
        color: #fff;
        padding: 10px;
        width: 25%;
        text-shadow: 0 0 3px #000;
        text-align: center
    }
}

/* Our Clients */
.our-clinets {
    padding: 100px 30px;
    text-align: right;
    h2 {
        margin-bottom: 90px;
    }
    .card {
        border: none;
        text-align: center;
        .card-img-overlay {
            background-color: rgba(#000, .65);
            width: 100%;
            height: 100%;
            opacity: 0;
            top: auto;
            left: auto;
            right: 0%;
            transition: .3s;
            transform: scale(.5);
            cursor: pointer;
            font-size: 30px;
            color: #fff;
            border-radius: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
            .card-title, .card-text {
                font-family: 'Cairo', sans-serif;
                text-align: center;
                color: $mainColor;
                margin: 0px;
                text-shadow: 0 0 3px #000;
                font-weight: 600
            }
            .card-title {
                font-size: 20px;
                margin-top: 50px;
                margin-bottom: 10px
            }
        }
        &:hover {
            .card-img-overlay {    
                opacity: 1;
                transform: scale(1);
            }
        }
    }
}

/* Google Map */
.mapouter{
    position:relative;
    text-align:right;
    height:500px;
    width:100%
}
.gmap_canvas {
    overflow:hidden;
    background:none!important;
    height:500px;
    width:100%
}