/* Navigation Bar */
.navbar {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 70px;
    background-color: rgba(#333, .64);
    z-index: 9999;
    .collapse {
        .navbar-nav {
            .nav-item {
                .nav-link {
                    color: #f0f0f0;
                    font-size: 15px;
                    font-weight: 600;
                    padding: 13px;
                    transition: .2s;
                    text-shadow: 0 0 3px #000;
                    &:hover {
                        color: $mainColor
                    }
                }
            }
        }
    }
}